body {
  margin: 0;
  width: 100vw;
  height: 100vh;
  background-color: lavender;
  font-family: "Gochi Hand", cursive;
  overflow: hidden;
}

#root {
  width: 100%;
  height: 100%;
}

.container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.heading {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  padding: 10px;
}

.heading img {
  width: 100px;
}

h1 {
  transform: rotate(2deg);
  padding: 0.2rem 1.2rem;
  border-radius: 20% 5% 20% 5%/5% 20% 25% 20%;
  background-color: hsla(166, 100%, 50%, 0.7);
  font-size: 1.5rem;
}

.form-container {
  width: 50%;
  height: 500px;
  display: flex;
  justify-content: center;
  background-image: radial-gradient(#bfc0c1 5%, transparent 0);
  background-size: 25px 25px;
  border-radius: 20px;
  box-shadow: 4px 3px 7px 2px #00000040;
  background-color: white;
  overflow-y: scroll;
}

form {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}

form * {
  margin: 10px;
}

form input {
  font-family: inherit;
  border: none;
  border-bottom: 3px dotted plum;
  font-size: 1rem;
}

form p:hover {
  cursor: pointer;
  text-decoration: line-through #24bffb;
  width: fit-content;
}

form input:focus,
form input:active {
  outline: none;
}

.button {
  display: flex;

  align-items: flex-end;
  justify-content: flex-end;
}

button {
  padding: 0;
  border: none;
  margin: 0;
  transform-origin: center;
  font-family: "Gochi Hand", cursive;
  text-decoration: none;
  padding-bottom: 3px;
  border-radius: 5px;
  box-shadow: 0 2px 0 hsl(198, 1%, 29%);
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  background-image: url("data:image/gif;base64,");
  background-color: hsla(166, 100%, 50%, 0.7);
}

button span {
  background: #f1f5f8;
  display: block;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  border: 2px solid hsl(198, 1%, 29%);
  margin: 0;
}

button:active {
  transform: translateY(4px);
  padding-bottom: 0px;
  outline: 0;
}
